import React from "react";
import { cn } from "@/shared/lib/utils";

export interface ColumnDef<T> {
  header: string;
  accessorKey?: keyof T;
  width?: string;
  align?: "left" | "center" | "right";
  cell?: (item: T, index: number) => React.ReactNode;
}

export function DataTable<T>({
  columns,
  data,
  minRows = 5,
}: {
  columns: ColumnDef<T>[];
  data: T[];
  minRows?: number;
}) {
  return (
    <div className="border border-slate-200 rounded-lg overflow-hidden bg-white shadow-sm flex flex-col">
      <div className="overflow-x-auto">
        <table className="w-full text-sm border-collapse">
          <thead className="bg-slate-50/80 text-slate-700 font-bold border-b border-slate-200">
            <tr>
              <th className="p-2.5 w-10 text-center border-r border-slate-200 text-[11px]">
                #
              </th>
              {columns.map((col, i) => (
                <th
                  key={i}
                  className={cn(
                    "p-2.5 border-r border-slate-200 last:border-0 text-[11px] whitespace-nowrap",
                    col.width || "w-auto",
                    col.align === "center" && "text-center",
                    col.align === "right" && "text-right",
                    col.align === "left" && "text-left",
                  )}
                >
                  {col.header}
                </th>
              ))}
            </tr>
          </thead>
          <tbody className="divide-y divide-slate-100">
            {data.map((row, idx) => (
              <tr
                key={idx}
                className="hover:bg-indigo-50/30 transition-colors group"
              >
                <td className="p-2.5 text-center text-slate-400 font-medium border-r border-slate-100 text-[11px] group-hover:text-indigo-500">
                  {idx + 1}
                </td>
                {columns.map((col, cIdx) => (
                  <td
                    key={cIdx}
                    className={cn(
                      "p-2.5 border-r border-slate-100 last:border-0 truncate text-[12px] text-slate-600",
                      col.align === "center" && "text-center",
                      col.align === "right" && "text-right",
                      col.align === "left" && "text-left",
                    )}
                  >
                    {col.cell
                      ? col.cell(row, idx)
                      : col.accessorKey
                        ? String(row[col.accessorKey] || "")
                        : ""}
                  </td>
                ))}
              </tr>
            ))}
            {/* 빈 행 채우기 */}
            {Array.from({ length: Math.max(0, minRows - data.length) }).map(
              (_, i) => (
                <tr
                  key={`empty-${i}`}
                  className="h-9 border-b border-slate-50 last:border-0 last:border-b-0"
                >
                  <td className="border-r border-slate-50"></td>
                  {columns.map((_, j) => (
                    <td
                      key={j}
                      className="border-r border-slate-50 last:border-0"
                    ></td>
                  ))}
                </tr>
              ),
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}
