"use client";

import React from "react";
import { Plus } from "lucide-react";
import { Button } from "@/shared/components/ui/button";
import { Badge } from "@/shared/components/ui/badge";
import { cn } from "@/shared/lib/utils";
import { DataTable, ColumnDef } from "../components/data-table";

interface HistoryProps {
  data: {
    history?: {
      maintenance?: string[][]; // [구분, 날짜, 내용, ..., 비용, 상태]
      materials?: string[][]; // [구분, 구매처, 품명, 수량, ..., 금액]
    };
  };
}

export function History({ data }: HistoryProps) {
  const maintenanceData = data.history?.maintenance || [];
  const materialsData = data.history?.materials || [];

  // 1. 유지보수 이력 컬럼 정의
  const maintColumns: ColumnDef<string[]>[] = [
    {
      header: "일자",
      width: "w-28",
      align: "center",
      cell: (row) => <span className="font-mono text-slate-600">{row[1]}</span>,
    },
    {
      header: "구분",
      width: "w-20",
      align: "center",
      cell: (row) => (
        <Badge
          variant="secondary"
          className={cn(
            "h-5 px-1.5 text-[10px] font-bold border rounded-md",
            row[0] === "수리"
              ? "bg-rose-50 text-rose-700 border-rose-100"
              : "bg-blue-50 text-blue-700 border-blue-100",
          )}
        >
          {row[0]}
        </Badge>
      ),
    },
    {
      header: "작업내용",
      align: "left",
      cell: (row) => (
        <span className="font-medium text-slate-700">{row[2]}</span>
      ),
    },
    {
      header: "비용",
      width: "w-24",
      align: "right",
      cell: (row) => <span className="font-mono text-slate-600">{row[5]}</span>,
    },
    {
      header: "상태",
      width: "w-20",
      align: "center",
      cell: (row) => (
        <span className="text-[11px] font-bold text-emerald-600 flex items-center justify-center gap-1">
          {row[6]}
        </span>
      ),
    },
  ];

  // 2. 자재 이력 컬럼 정의
  const matColumns: ColumnDef<string[]>[] = [
    {
      header: "구분",
      width: "w-20",
      align: "center",
      cell: (row) => <span className="text-slate-600">{row[0]}</span>,
    },
    {
      header: "구매처",
      width: "w-32",
      align: "center",
      cell: (row) => <span className="text-slate-500">{row[1]}</span>,
    },
    {
      header: "품명/규격",
      align: "left",
      cell: (row) => (
        <span className="font-medium text-slate-700">{row[2]}</span>
      ),
    },
    {
      header: "수량",
      width: "w-20",
      align: "right",
      cell: (row) => <span className="font-mono text-slate-600">{row[3]}</span>,
    },
    {
      header: "금액",
      width: "w-24",
      align: "right",
      cell: (row) => (
        <span className="font-mono font-bold text-slate-700">{row[5]}</span>
      ),
    },
  ];

  return (
    <div className="flex flex-col gap-4 h-full overflow-hidden">
      {/* --- [섹션 1] 수리 및 정비 이력 (화면의 50% 차지) --- */}
      <div className="flex-1 flex flex-col min-h-0">
        <div className="flex items-center justify-between mb-2 shrink-0">
          <h3 className="text-[13px] font-bold text-slate-700 flex items-center gap-2">
            <span className="w-1.5 h-3.5 bg-indigo-600 rounded-full" />
            수리 및 정비 이력
          </h3>
          <Button
            variant="ghost"
            size="sm"
            className="h-7 text-[11px] text-indigo-600 hover:bg-indigo-50 gap-1.5"
            onClick={() => alert("수리 이력 추가 팝업")}
          >
            <Plus className="h-3.5 w-3.5" /> 이력 추가
          </Button>
        </div>

        {/* flex-1 min-h-0: 이 영역이 남은 공간을 꽉 채우고, 내부에서 스크롤됨 */}
        <div className="flex-1 min-h-0">
          <DataTable
            columns={maintColumns}
            data={maintenanceData}
            minRows={0} // 데이터가 적을 때 억지로 빈 행을 만들지 않음 (스크롤 확인용)
          />
        </div>
      </div>

      {/* --- [섹션 2] 자재 및 부품 이력 (화면의 50% 차지) --- */}
      <div className="flex-1 flex flex-col min-h-0">
        <div className="flex items-center justify-between mb-2 shrink-0">
          <h3 className="text-[13px] font-bold text-slate-700 flex items-center gap-2">
            <span className="w-1.5 h-3.5 bg-amber-500 rounded-full" />
            자재 및 부품 이력
          </h3>
          <Button
            variant="ghost"
            size="sm"
            className="h-7 text-[11px] text-amber-600 hover:bg-amber-50 gap-1.5"
            onClick={() => alert("자재 이력 추가 팝업")}
          >
            <Plus className="h-3.5 w-3.5" /> 자재 구매 등록
          </Button>
        </div>

        {/* flex-1 min-h-0: 위와 동일하게 독립 스크롤 영역 확보 */}
        <div className="flex-1 min-h-0">
          <DataTable columns={matColumns} data={materialsData} minRows={0} />
        </div>
      </div>
    </div>
  );
}
